
body {
	background: #2f4050;

	#wrapper {
		.navbar-static-side {
			z-index: 101;
			position: absolute;
			width: 220px;
			border-radius: 0;
			background: #2f4050;
		}
	}

	#page-wrapper {
		position: inherit;
		margin: 0 0 0 220px;
		min-height: 1200px;
		padding: 0 15px;
	}

	.gray-bg {
		background-color: #f3f3f4;
	}

	.white-bg {
		background-color: #ffffff;
	}

	.navbar-fixed-top,
	.navbar-static-top {
		background: #f3f3f4;
	}

	.dropdown-menu {
		min-width: 100%;
	}

	.navbar.navbar-default {
		margin-bottom: 0;
		border-radius: 0;
		border-top: none;
		background: white;
	}

	#content {
		padding: 20px 0;
		td {
			span.btn-xs {
				margin-bottom: 3px;
			}
		}
	}

	#user-block {
		padding: 40px;
		background: white;
		.gravatar {
			border-radius: 100%;
			max-width: 100%;
			height: auto;
			border: 1px solid #f3f3f4;
		}
	}

	.nav-pills {
		> li {
			margin: 0 0 1px 0;
			> a, >a:visited {
				border-radius: 0;
				padding: 15px 20px;
				background: #1abc9c;
				color: white;
				i {
					width: 20px;
				}
			}
			&.has-children {
				> ul {
					background:white;
					padding: 20px;
					li {
						list-style: none;
					}
				}
			}
		}
	}
}